<template>
	<div class="header">
      <div class="search-box">
        <el-input placeholder="请输入内容" prefix-icon="el-icon-search" v-model="keyword" @input="handleInput" ></el-input>
      </div>

      <div class="main-box">
      	<div class="tool-group">
      		<div class="tool-item" @click="handleBack">
      			<img src="../assets/images/back.png" alt />
      		</div>
      		<div class="tool-item" @click="handleRefresh">
      			<img src="../assets/images/refresh.png" alt />
      		</div>
      		<div class="tool-item" @click="handleFullgreen">
      			<img src="../assets/images/fullgreen.png" alt />
      		</div>
      	</div>
      	<div class="print-time">
	        
	        <div class="timer">{{timer.toLocaleString().replace(/\//g, '-')}}</div>
	      </div>
      </div>
      
    </div>
</template>

<script>
export default {
 	data(){
 		return {
 			keyword:'',
 			timer: new Date().toLocaleString().replace(/\//g, "-")
 		}
 	},
 	methods:{
 		handleInput(){
	      this.$emit('search', this.keyword);
	    },
	    handleBack(){
	    	this.$router.go(-1);//返回上一层
	    },
	    handleRefresh(){
	    	this.$emit('refresh');
	    },
	    handleFullgreen(){
	    	let element = document.documentElement;
            if (this.fullscreen) {
                if (document.exitFullscreen) {
                    document.exitFullscreen();
                } else if (document.webkitCancelFullScreen) {
                    document.webkitCancelFullScreen();
                } else if (document.mozCancelFullScreen) {
                    document.mozCancelFullScreen();
                } else if (document.msExitFullscreen) {
                    document.msExitFullscreen();
                }
            } else {
                if (element.requestFullscreen) {
                    element.requestFullscreen();
                } else if (element.webkitRequestFullScreen) {
                    element.webkitRequestFullScreen();
                } else if (element.mozRequestFullScreen) {
                    element.mozRequestFullScreen();
                } else if (element.msRequestFullscreen) {
                    // IE11
                    element.msRequestFullscreen();
                }
            }
            this.fullscreen = !this.fullscreen;
	    },
	    getTime() {
	      setInterval(() => {
	        this.timer = new Date();
	      }, 1000);
	    }
 	},
 	created() {
	    this.getTime();
	 }
}
</script>


<style>
.header {
  width:100%;
  height:40px;
  display: flex;
  justify-content: space-between;
  background:#fff;
  border-bottom: 1px solid rgba(238, 238, 238, 1);
}
.header img {
  width: 20px;
  height: 20px;
}
.header .el-input__inner {
	height:40px !important;
	width:215px !important;
}
.main-box {
  display:flex;
}
.tool-group {
  display:flex;
  justify-content: center;
   align-items: center;
}
.tool-group .tool-item {
	height:100%;
	padding: 5px 10px;
	cursor: pointer;
	display:flex;
	justify-content: center;
   align-items: center;
}
</style>



